.default,
.empty {
  position: relative;
  width: 100%;
  height: 500px;
  margin-top: 10rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.default::before,
.empty::before {
  content: "";
  background-image: url('bg.jpg');
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.3;
}

.default > h2 , .default > p{
  position: relative;
  opacity: 1;
  font-size: 1.25rem;
  color: #fafa22;
  background-color: #898989;
  font-family: var(--font-raleway);
}

.empty > h2
{
  position: relative;
  opacity: 1;
  font-size: 1.25rem;
  color: #fafa22;
  background-color: #898989;
  font-family: var(--font-raleway);
}

.container {
  width: 100%;
  margin-top: 3rem;
  position: relative;
  top: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  z-index: 0;
  overflow-y: hidden;
}

.movie {
  width: 310px;
  height: 460px;
  margin: 1.5rem;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;

  transition: all 0.7s cubic-bezier(0.175, 0.905, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}


.movie div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #f3ab71;
}

.movie:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.movie div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.movie div:nth-of-type(2) img {
  height: 100%;
  width: 100%;
}

.movie div:nth-of-type(3) {
  z-index: 2;
  background-color: #343739;
  padding: 16px 24px 24px 24px;

  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.movie div:nth-of-type(3) span {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
}

.movie div:nth-of-type(3) h3 {
  margin-top: 5px;
  font-family: "Roboto Slab", serif;
  color: #f3ab71;
}

.movie:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.1;
}

.movie:hover div:nth-of-type(3) {
  background-color: transparent;
}

.movie:hover div:nth-of-type(1) {
  opacity: 1;
}

@media screen and (max-width: 800px) {
  .app {
    padding: 4rem 2rem;
  }

  h1 {
    font-size: 2.3rem;
  }

  .navbar {
    flex-direction: column;
    padding: 10px;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 1rem 1rem;
  }

  .navbar {
    column-gap: 10px;
    padding-top: 20px;
  }

  h1 {
    font-size: 1.5rem;
  }

  .search {
    padding: 0.5rem 0.75rem;
    width: 100%;
  }

  .search input {
    font-size: 0.8rem;
  }

  .search img {
    width: 15px;
    height: 15px;
  }

  .container {
    margin-top: 1rem;
  }

  .movie {
    width: "100%";
    margin: 1.5rem;
  }


}